.words {
  position: absolute;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  top: 10px;
  left: 10px;
  color: #ddd;
  display: flex;
  flex-direction: column;
  width: 250px;
  height: auto;
  padding: 10px;
  gap: 15px;
  z-index: 0;
}

.the-word {
  letter-spacing: 15.5px;
  text-transform: uppercase;
  height: 32px;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  padding-left: 8px;
}
.pos-dash {
  position: absolute;
  top: 12px;
  display: flex;
  flex-direction: column;
  gap: 27px;
  padding-top: 6px;
}
.all-dash {
  position: relative;
  display: flex;
  gap: 6px;
}
.single-dash {
  width: 27px;
  font-size: 20px;
  height: 20px;
  padding: 0.1rem;
  color: #b7caf3;
  font-size: 20px;
  font-weight: bolder;
  border-bottom: 2px solid #ddd;
  border-left: 2px solid #ddd;
  opacity: 0.6;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
}

.btn-word {
  position: absolute;
  bottom: 0;
}

.title-uni {
  position: absolute;
  bottom: -30px;
  color: #a14444;
  margin-bottom: 10px;
}

.counter {
  font-size: 24px;
  font-weight: bold;
  color: #ddd;
  position: absolute;
  right: -90px;
  top: 10px;
}
.spinner {
  position: absolute;
  top: 80px;
  left: 80px;
}
.wheel {
  position: absolute;
  right: -90px;
  bottom: -145px;
  transform: translate(-50% -50%);
  display: flex;
  flex-wrap: wrap;
  width: 150px;
  height: 150px;
  gap: 10px;
  border-radius: 50%;
  border: 1px solid lightgrey;
  background-color: #5e5555;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
}

.seconds {
  position: absolute;
  top: 55px;
  border-radius: 50%;
  left: 55px;
  background-color: #a14444;
  font-size: 16px;
  padding: 10px;
}

.next {
  position: absolute;
  right: -80px;
  top: 100px;
}

.btn-next {
  height: 100px;
  width: 60px;
  background-color: #468146;
  border: none;
  font-size: 24px;
  color: #ddd;
  border-radius: 4px;
  animation: dribling 1s infinite;
  transition: all 1.4s ease;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
}

@keyframes dribling {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

.wheel h4 {
  width: 120px;
  position: absolute;
  top: -55px;
  right: -10px;
}
.item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 36px;
  height: 36px;
  position: absolute;
  left: 55px;
  bottom: 55px;
  border-radius: 50%;
}
.item span {
  font-size: 36px;
  height: 48px;
}

.item.i-0 {
  transform: rotate(0deg) translate(50px) rotate(0deg);
  background-color: #a14444;
}

.item.i-1 {
  transform: rotate(51.42deg) translate(50px) rotate(-51.42deg);
  background-color: #a14444;
}

.item.i-2 {
  transform: rotate(102.84deg) translate(50px) rotate(-102.84deg);
  background-color: #a14444;
}

.item.i-3 {
  transform: rotate(154.26deg) translate(50px) rotate(-154.26deg);
  background-color: #a14444;
}

.item.i-4 {
  transform: rotate(205.68deg) translate(50px) rotate(-205.68deg);
  background-color: #a14444;
}

.item.i-5 {
  transform: rotate(257.1deg) translate(50px) rotate(-257.1deg);
  background-color: #a14444;
}

.item.i-6 {
  transform: rotate(308.52deg) translate(50px) rotate(-308.52deg);
  background-color: #a14444;
}

.item.i-7 {
  transform: rotate(360deg) translate(0px) rotate(-360deg);
  background-color: #a14444;
}
